<template>
  <div
    id="bar"
    style="height:100%;width:100%;"
    ref="barEl"
  />
</template>

<script>
import { onMounted, ref, watch } from 'vue'
import { init } from 'echarts'
import { getTaskTypeName } from '@/utils/taskType'

export default {
  name: 'Bar',
  props: {
    data: {
      type: Array,
      required: true
    },
    title: {
      type: String,
      required: true
    }
  },
  setup (props) {
    const barEl = ref(null)

    let chart

    function getType (set, data) {
      set.clear()
      data.forEach(element => {
        if (element.taskType.length > 0) {
          set.add(getTaskTypeName(element.taskType[0]))
        }
      })
    }

    const series = []
    let YAxis = null

    const makeSeriesItem = (optionData, data, taskType) => {
      data.forEach(element => {
        if (getTaskTypeName(element.taskType[0]) === taskType) {
          optionData[props.title === '投入度' ? element.devotion : element.satisfaction]++
        }
      })
    }

    const makeSeries = (series, YAxis) => {
      series.splice(0)
      YAxis.forEach(element => {
        const optionData = new Array(6).fill(0)
        makeSeriesItem(optionData, props.data, element)
        series.push({
          name: element,
          type: 'bar',
          stack: 'total',
          label: {
            show: true
          },
          emphasis: {
            focus: 'series'
          },
          data: optionData
        })
      })
    }

    const option = {
      title: {
        text: props.title,
        left: 'center',
        top: '20'
      },
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'shadow'
        }
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis: {
        type: 'value'
      },
      yAxis: {
        type: 'category',
        data: ['0', '1', '2', '3', '4', '5']
      },
      series: series
    }

    const mountClickEvent = (chart) => {
      chart.on('click', params => {
        console.log(params.data)
      })
    }

    const initChart = () => {
      chart = init(barEl.value)
      mountClickEvent(chart)
      const st = new Set()
      getType(st, props.data)
      YAxis = Array.from(st)
      makeSeries(series, YAxis)
      chart.setOption(option)
    }

    const mountData = () => {
      makeSeries(series, YAxis)
      chart.setOption(option, true)
    }

    watch(props.data, () => {
      mountData()
    })

    onMounted(() => {
      initChart()
    })

    return {
      barEl
    }
  }
}
</script>

<style lang="less" scoped>
#bar div{
  height: 100%;
  width: 100%;
}
</style>
